.search-bar {
    height: 1.5rem;
    width: 85%;
    margin-left: .9rem;
    border-radius: 5px;
    padding-left: .5rem;
    &-container {
        display: flex;
        flex-direction: column;
        justify-content: space-evenly;
        margin-left: 5rem;
        margin-top: 2rem;
        padding-top: 1rem;
        width: 12rem;
        border: 2px solid black;
        height: 9rem;
        border-radius: 20px;
        background-color: rgb(90, 118, 155);
    }
    &__category {
        height: 1.5rem;
        width: 85%;
        margin-left: 1rem;
        background-color: white;
    }
}